import React from "react"
import { Link } from "react-router-dom";
import alice from "../../assets/images/alice2k.png"
import animespirit from "../../assets/images/animespirit.jpg"
import github from "../../assets/images/github.png"
import footer from "../../assets/images/footer.png"

const Footer = ({}) => {

  return (
    <div className="footerContainer">
      <div className="footerLeft">
        <a href="https://animespirit.ru/" target="_blank">
          <img src={animespirit}/>
        </a>
        <a href="https://github.com/anilibria" target="_blank">
          <img src={github}/>
        </a>
        <a href="https://alice2k.work/" target="_blank">
          <img src={alice}/>
        </a>
      </div>
      <div className="footerCenter">
        <img src={footer}/>
      </div>
      <div className="footerRight">
        <div className="footerButtons">
          <Link to="/login" ></Link>
          <a href="tg://resolve?domain=Libria911Bot"></a>
          <Link to="/login"> </Link>
          <Link to="/login"></Link>
          <Link to="/login"></Link>
        </div>
        <div className="footerDescription">
                   .
               -    AniTest@proton.me
        </div>
      </div>
    </div>
  )
}

export default Footer