import React, { useEffect } from 'react';
import { Outlet, Link } from "react-router-dom";
import './index.scss';
import Sidebar from './sidebar';
import Footer from './footer';
import Support from '../../assets/images/support.png'
import { useAppDispatch, useAppSelector } from '../../hooks/redux';
import { getSidebarTitles } from '../../redux/reducers/ActionCreators';


const Layout = () => {

  const dispatch = useAppDispatch()
  const {
    fetchingSidebarTitles, sidebarTitlesError, sidebarTitles
  } = useAppSelector(state => state.SearchReducer)

  const getTitles = () => {
    const payload = {
      query: `{season.code} >= 0 and {season.year} >= 0`,
      limit: 5,
      order_by: 'updated',
    }
    dispatch(getSidebarTitles())
  }

  useEffect(() => {
    !sidebarTitles.length && getTitles()
  }, [])

  return (
    <div className='layoutContainer'>
      <div className='bodyContainer'>
        <div className='mainContainer'>
          <div className='navBar'>
            <nav>
              <Link to="/"></Link>
              <Link to="/releases"></Link>
              <Link to="/schedule"></Link>
              <Link to="/random"></Link>
              <a href="https://anilibria.app/" target='_blank'></a>
              <Link to="/team"></Link>
              <a href="https://www.anilibria.tv/pages/donate.php"  target='_blank'> </a>
            </nav>
          </div>
          <div className='donate'>
            <Link to="/donate">
              <img src={Support}/>
            </Link>
          </div>
          <div className='contentContainer'>
            <Outlet />
          </div>
        </div>
        <Sidebar sidebarTitles={sidebarTitles}/>
      </div>
      <Footer />
    </div>
  )
}

export default Layout